          <span v-if="damageRadius">Damage Zone <br> Radius={{damageRadius}}</span>
      </div>
      <div class="circle-3" :style="{width:B17Val+'px',height:B17Val+'px','z-index':c3}">
        <!-- <span v-if="B17Val">Blast hole</span> -->
      </div>
      <div class="circle-4" :style="{width:B16Val+'px',height:B16Val+'px','z-index':c4}">
        <!-- <span v-if="B16Val">Charge</span> -->
      </div>
      <div class="tool-tips">
        <span style="margin-right:15px"><span style="margin-right:6px;width:30px;border: 1px solid red;display: inline-block;vertical-align: middle;"></span>Damage Zone</span>
        <span style="margin-right:15px"><span style="margin-right:6px;width:30px;border: 1px solid #FFC000;display: inline-block;vertical-align: middle;"></span>Blast hole</span>
        <span style="margin-right:15px"><span style="margin-right:6px;width:30px;border: 1px solid #FFFF00;display: inline-block;vertical-align: middle;"></span>Charge</span>
      </div>
      <!-- <div class="circle-1"></div>
      <div class="circle-2">
          <span>Damage Zone <br> Radius={{damageRadius}}</span>
      </div>
      <div class="circle-3">
        <span>Blast hole</span>
      </div>
      <div class="circle-4">
        <span>Charge</span>
      </div> -->
