</template>
  
  <script>
    export default {
        components: {},
        name: 'PageFooter',
        props: {
            msg: String,
            B16:Number,
            B17:Number,
            B33:Number,
            isRefresh:String,
        },
        data(){
            return{
                B16Val:0,
                B17Val:0,
                B33Val:0,
                B33Val2:0,
                damageRadius:'',
                c1:'101',
                c2:'102',
                c3:'103',
                c4:'104',
            }
        },
        watch:{
            deep:true,
            B16(newVal,oldValue){
              if (this.timer) {
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    if(newVal&&newVal!=oldValue){
                      this.B16Val = newVal*2;
                      const arrayValue=new Array();
                      arr[0]=this.B16Val;
                      arr[1]=this.B17Val;
                      arr[2]= this.B33Val;
                      arr[3]= this.B33Val2;
                      arrayValue.sort(function(a,b){return a-b;});
                      
                    }
                }, 500)
            },
            B17(newVal,oldValue){
                if (this.timer) {
                      clearTimeout(this.timer)
                  }
                  this.timer = setTimeout(() => {
                      if(newVal&&newVal!=oldValue){
                        this.B17Val = newVal*2;
                      }
                  }, 500)
            },
            B33(newVal,oldValue){
                if(this.timer) {
                    clearTimeout(this.timer)
                }
                // this.timer = setTimeout(() => {
                //     if(newVal&&newVal!=oldValue){
                //       this.damageRadius = newVal;
                //       this.B33Val = newVal*300;
                //       this.B33Val2 = 2*newVal*300;
                //     }
                // }, 1000)
                if(newVal&&newVal!=oldValue){
                      this.damageRadius = newVal;
                      this.B33Val = newVal*400;
                      this.B33Val2 = 2*newVal*400;
                    }
            },
            isRefresh(newVal,oldValue){
              if(newVal&&newVal!=oldValue){
                const formData = this.$store.state.formData;
                this.B16Val =  formData.B16;
                this.B17Val = formData.B17;
                this.damageRadius = formData.B33;
                let b33 = formData.B33*1000;
                if(b33>200){
                  b33=200
                }
                this.B33Val = b33
                this.B33Val2 = b33*2;
              }
            }
        },
    }
  </script>
  
