<template>
    <div class="header-container">
       <div class="content">
          <!-- <div class="search">
             <el-input v-model="keyword" placeholder="型号/品牌" style="width:475px;"></el-input>
             <el-button icon="el-icon-search" @click="handleClick"></el-button>
          </div> -->
        <div style="margin-top: 18px;font-size: 18px;font-weight: bold;">Pressure attenuation model - Decoupled</div>
        <div style="text-align: left;padding-left: 18px; margin-top: 20px;margin-bottom: 10px;font-weight: bold;">Rock input parametrs</div>
        <el-form ref="form" size="mini" :inline="true" label-position="left" :model="formData">
            <div class="form-item">
                <el-form-item label="UCS" label-width="150px">
                    <el-input v-model="formData.B4" placeholder="">
                        <template slot="append">MPa</template>
                    </el-input>
                </el-form-item>
            </div>
            <div class="form-item">
                <el-form-item label="Dynamic Youngs E" label-width="150px">
                    <el-input v-model="B5" disabled>
                        <template slot="append">GPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="StaticTensile strength" label-width="150px">
                    <el-input v-model="formData.B6">
                        <template slot="append">MPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="v" label-width="150px">
                    <el-input v-model="formData.B7" placeholder="">
                        <template slot="append">MPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Pwave velocity" label-width="150px">
                    <el-input v-model="formData.B8" placeholder="">
                        <template slot="append">m/s&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Swave velocity" label-width="150px">
                    <el-input v-model="B9" disabled>
                        <template slot="append">m/s&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="K" label-width="150px">
                    <el-input v-model="B10" disabled>
                        <template slot="append">GPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Rock Density" label-width="150px">
                    <el-input v-model="formData.B11" placeholder="请输入内容">
                        <template slot="append">g/cc</template>
                    </el-input>
                </el-form-item>
                <div style="text-align: left;padding-left: 18px; margin-top: 8px;margin-bottom: 10px;font-weight: bold;">Explosive input parametrs</div>
                <el-form-item label="Explosive VOD" label-width="150px">
                    <el-input v-model="formData.B14" placeholder="">
                        <template slot="append">m/s&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Explosive Density" label-width="150px">
                    <el-input v-model="formData.B15" placeholder="">
                        <template slot="append">kg/m³</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Charge diameter" label-width="150px">
                    <el-input v-model="formData.B16" placeholder="">
                        <template slot="append">mm&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Blasthole diameter" label-width="150px">
                    <el-input v-model="formData.B17" placeholder="">
                        <template slot="append">mm&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Column Length" label-width="150px">
                    <el-input v-model="formData.B18" placeholder="">
                        <template slot="append">m&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Column W (kg)" label-width="150px">
                    <el-input v-model="B19" disabled>
                        <template slot="append">kg&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <div style="text-align: left;padding-left: 18px; margin-top: 8px;margin-bottom: 10px;font-weight: bold;">output</div>
                <el-form-item label="Decoupling ratio" label-width="150px">
                    <el-input v-model="B21" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Decouple Multiplier" label-width="150px">
                    <el-input v-model="B22" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Decay phi" label-width="150px">
                    <el-input v-model="B23" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Decay Omega" label-width="150px">
                    <el-input v-model="B24" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="pi1" label-width="150px">
                    <el-input v-model="B25" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Detonation Pressure" label-width="150px">
                    <el-input v-model="B26" disabled>
                        <template slot="append">GPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Estimated Borehole Pressure" label-width="150px">
                    <el-input v-model="B27" disabled>
                        <template slot="append">GPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="CZI" label-width="150px">
                    <el-input v-model="B28" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Dimensionless 1" label-width="150px">
                    <el-input v-model="B29" disabled>
                        <template slot="append">-&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Crushed zone radius (Rc)" label-width="150px">
                    <el-input v-model="B30" disabled>
                        <template slot="append">mm&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Equilibrium pressure" label-width="150px">
                    <el-input v-model="B31" disabled>
                        <template slot="append">GPa</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Linear charge concentration" label-width="150px">
                    <el-input v-model="B32" disabled>
                        <template slot="append">kg/m</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Radius of damage (Rd)" label-width="150px">
                    <el-input v-model="B33" disabled>
                        <template slot="append">m&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Perimeter Spacing recommended (1.5*Rd)" label-width="150px">
                    <el-input v-model="B34" disabled>
                        <template slot="append">m&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="Perimeter Spacing recommended (2.0*Rd)" label-width="150px">
                    <el-input v-model="B35" disabled>
                        <template slot="append">m&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="" label-width="150px" style="display: block;">
                    <el-button type="primary" @click="onSubmit" style="width:120px">提&nbsp;&nbsp;交</el-button>
                </el-form-item>
            </div>
        </el-form>
       </div>
    </div>
</template>
  
  <script>
    export default {
        components: {},
        name: 'PageHeader',
        props: {
            msg: String,
            B16Fun:Function,
            B17Fun:Function,
            B33Fun:Function,
            B6Fun:Function,
            refreshFun:Function,
        },
        data() {
			return {
				keyword: '',
                dataList:[
                    {id:1,label:'UCS',isInput:true,value:'',unit:'MPa',type:'td'},
                    {id:2,label:'Dynamic Youngs E',isInput:false,value:'',unit:'GPa',type:'td'},
                    {id:3,label:'StaticTensile strength',isInput:false,value:'',unit:'MPa',type:'td'},
                    {id:4,label:'v ',isInput:true,value:'',unit:'MPa',type:'td'},
                    {id:5,label:'Pwave velocity',isInput:true,value:'',unit:'m/s',type:'td'},
                    {id:6,label:'Swave velocity',isInput:false,value:'',unit:'m/s',type:'td'},
                    {id:7,label:'K',isInput:false,value:'',unit:'GPa',type:'td'},
                    {id:8,label:'Rock Density',isInput:true,value:'',unit:'g/cc',type:'td'},
                    {id:9,type:'null'},
                    {id:10,label:'EXPLOSIVE INPUT PARAMETERS',type:'hr'},
                    {id:11,label:'Explosive VOD',isInput:true,value:'',unit:'m/s',type:'td'},
                    {id:12,label:'Explosive Densit',isInput:true,value:'',unit:'kg/m3',type:'td'},
                    {id:13,label:'Charge diameter',isInput:true,value:'',unit:'mm',type:'td'},
                    {id:14,label:'Blasthole diameter',isInput:true,value:'',unit:'mm',type:'td'},
                    {id:15,label:'Column Length',isInput:true,value:'',unit:'m',type:'td'},
                    {id:16,label:'Column W (kg)',isInput:false,value:'',unit:'kg',type:'td'},
                    {id:17,label:'OUTPUT',type:'hr'},
                    {id:18,label:'Decoupling ratio',isInput:false,value:'',unit:'-',type:'td'},
                    {id:19,label:'Detonation Pressure',isInput:false,value:'',unit:'GPa',type:'td'},
                    {id:20,label:'Estimated Borehole Pressure',isInput:false,value:'',unit:'GPa',type:'td'},
                    {id:21,label:'Crushed zone radius (Rc)',isInput:false,value:'',unit:'mm',type:'td'},
                    {id:22,label:'Equilibrium pressure',isInput:false,value:'',unit:'GPa',type:'td'},
                    {id:23,label:'Linear charge concentration',isInput:false,value:'',unit:'kg/m',type:'td'},
                    {id:24,label:'Radius of damage (Rd)',isInput:false,value:'',unit:'m',type:'td'},
                    {id:25,label:'Perimeter Spacing recommended (1.5*Rd)',isInput:false,value:'',unit:'m',type:'td'},
                    {id:26,label:'Perimeter Spacing recommended (2.0*Rd)',isInput:false,value:'',unit:'m',type:'td'},
                ],
                formData:{
                    B4:'',
                    B5:'',
                    B6:'',
                    B7:'',
                    B8:'',
                    B9:'',
                    B10:'',
                    B11:'',
                    B14:'',
                    B15:'',
                    B16:'',
                    B17:'',
                    B18:'',
                    B19:'',
                    B21:'',
                    B22:'',
                    B23:'',
                    B24:'',
                    B25:'',
                    B26:'',
                    B27:'',
                    B28:'',
                    B29:'',
                    B30:'',
                    B31:'',
                    B32:'',
                    B33:'',
                    B34:'',
                    B35:'',
                },
			};
		},
        computed:{
            B5(){
                let calVal = "";
                if(this.formData.B11!=''&&this.formData.B8!=''&&this.B9!=''){
                    calVal = (((this.formData.B11*1000)*((3*Math.pow(this.formData.B8,2))-(4*Math.pow(this.B9,2))))/((Math.pow(this.formData.B8,2)/Math.pow(this.B9,2))-1))/1000000000;
                }
                calVal=calVal?calVal.toFixed(2):'';
                return calVal;
            },
            // B6(){
            //     const calVal = (this.B4/14).toFixed(2);
            //     if(calVal){
            //         this.B6Fun(calVal);
            //         return calVal;
            //     }
            //     return "";
            // },
            B9(){
                const calVal = (this.formData.B8/1.577).toFixed(2);
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return '';
            },
            B10(){
                if(this.B5&&this.formData.B7){
                    const calVal = this.B5/(1+parseFloat(this.formData.B7));
                    if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal.toFixed(2);
                }
                return '';
                }else{
                    return "";
                }
                
            },
            B19(){
                const calVal = ((Math.pow(((this.formData.B16/2)/1000),2)*Math.PI*this.formData.B18*this.formData.B15)).toFixed(2)
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return '';
            },
            B21(){
                const calVal = (this.formData.B16/this.formData.B17).toFixed(2);
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B22(){
                if(this.B21){
                    if(this.B21<1){
                        return 0.7;
                    }else{
                        return 2;
                    }
                }
            },
            B23(){
                const calVal = (-1.54*(Math.pow(this.formData.B8/this.formData.B14,-0.33))).toFixed(2);
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B24(){
                const calVal = (-1.54*Math.pow(this.formData.B8/this.formData.B14,-0.1)).toFixed(2);
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B25(){
                const calVal = (1/((this.formData.B4*1000000)/(this.B5*1000000000))*(this.formData.B14/this.formData.B8)).toFixed(2)
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B26(){
                const calVal = (this.B27*2).toFixed(2);
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B27(){
                const calVal = ((((Math.pow(this.formData.B14,2)*(this.formData.B15))/8)/1000000000)*(Math.pow(this.B21,2))).toFixed(2)
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B28(){
                const calVal = (((Math.pow(this.B27*1000000000,3))/((this.B10*1000000000)*(Math.pow(this.formData.B4*1000000,2))))).toFixed(2)
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B29(){
                const calVal = (1.231*(Math.pow(this.B28,-0.219))).toFixed(2);
                if(calVal!='NaN'&&calVal!="Infinity"){
                    return calVal;
                }
                return "";
            },
            B30(){
                if(this.B28<2.6){
                    const calVal = ((((this.formData.B17/1000)/2)/(1))*1000).toFixed(2);
                    if(calVal!='NaN'&&calVal!='Infinity'){
                        return calVal;
                    }
                    return "";
                }else{
                   const calVal = ((((this.formData.B17/1000)/2)/(this.B29))*1000).toFixed(2);
                    if(calVal!='NaN'&&calVal!='Infinity'){
                        return calVal;
                    }
                    return "";
                }
            },
            B31(){
                const calVal = this.B27*(Math.pow(this.B30/(this.formData.B17/2),this.B23));
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal.toFixed(2);
                }
                return "";
            },
            B32(){
                const calVal = (this.B19/this.formData.B18).toFixed(2)
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return "";
            },
            B33(){
                const calVal = ((this.B30/1000)*(Math.pow((this.formData.B6*1000000)/(this.B31*1000000000),(1/this.B23)))).toFixed(2);
                if(calVal&&calVal!='NaN'&&calVal!='Infinity'){
                    this.B33Fun(calVal);
                }
                if(calVal!='NaN'&&calVal!='Infinity'){
                    return calVal;
                }
                return '';
            },
            B34(){
                const calVal = (1.5*this.B33).toFixed(2);
                if(calVal!='NaN'&&calVal!='Infinity'){
                   return calVal;
                }
                return "";
            },
            B35(){
                const calVal = (2*this.B33).toFixed(2)
                if(calVal!='NaN'&&calVal!='Infinity'){
                   return calVal;
                }
                return "";
            },
        },
        watch:{
            deep:true,
            dataList(newVal,oldValue){
                if(newVal&&newVal.length>0){
                   
                    const B4 = newVal.find(x=>x.label=="UCS");
                    const B7 = newVal.find(x=>x.label=="v");
                    const B8 = newVal.find(x=>x.label=="Pwave velocit");
                    const B11 = newVal.find(x=>x.label=="Rock Density");

                    const B14 = newVal.find(x=>x.label=="Explosive VOD");
                    const B15 = newVal.find(x=>x.label=="Explosive Density");
                    const B16 = newVal.find(x=>x.label=="Charge diameter");
                    const B17 = newVal.find(x=>x.label=="Blasthole diameter");
                    const B18 = newVal.find(x=>x.label=="Column Length");
                    
                    const filterList = newVal.filter((item)=>{return !item.isInput});
                    filterList.forEach(x=>{
                        if(x.label=="Dynamic Youngs E"){
                            if(B4){
                                B4/14
                            }
                            //B5的值
                            if(B8&&B9&&B11){
                                x.value = (((B11*1000)*((3*B8^2)-(4*B9^2)))/((B8^2/B9^2)-1))/1000000000
                            }
                            
                        }
                    });
                }
            },
            B6(newVal,oldValue){
                if (this.timer) {
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    if(newVal&&newVal!=oldValue){
                        this.B6Fun(parseFloat(newVal));
                    }
                }, 1000)
            },
            B16(newVal,oldValue){
                if (this.timer) {
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    if(newVal&&newVal!=oldValue){
                        this.B16Fun(parseFloat(newVal));
                    }
                }, 1000)
            },
            B17(newVal,oldValue){
                if (this.timer) {
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(() => {
                    if(newVal&&newVal!=oldValue){
                        this.B17Fun(parseFloat(newVal));
                    }
                }, 1000)
            },
        },
        methods:{
            onSubmit(){
              this.formData.B5 = this.B5;
              this.formData.B9 = this.B9;
              this.formData.B10 = this.B10;
              this.formData.B19 = this.B19;
              this.formData.B21 = this.B21;
              this.formData.B22 = this.B22;
              this.formData.B23 = this.B23;
              this.formData.B24 = this.B24;
              this.formData.B25 = this.B25;
              this.formData.B26 = this.B26;
              this.formData.B27 = this.B27;
              this.formData.B28 = this.B28;
              this.formData.B29 = this.B29;
              this.formData.B30 = this.B30;
              this.formData.B31 = this.B31;
              this.formData.B32 = this.B32;
              this.formData.B33 = this.B33;
              this.formData.B34 = this.B34;
              this.formData.B35 = this.B35;
              this.$store.commit("refreshformData", this.formData);
              this.refreshFun();
            }
        }
    }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    .header-container{
        width: 620px;
        /* height: 600px; */
        background-color: #fff;
        overflow-y: auto;
    }

    /deep/ .custom-input input{
        text-align: center;
        width: 120px;
        border: none;
        font-size: 14px;
    }

  </style>