            handleData1(){
                //定义数据间隔
                const dataInterval = [0, 0.1, 10, 100, 1000, 10000];
                for (let i = 0; i < this.dataList.length; i++) {
                const ssData = this.dataList[i];
                // 1.寻找在数据间隔里小于amount的最大值
                const min_v = Math.max(...dataInterval.filter((v) => v <= ssData[1]));
                // 2.寻找在数据间隔里大于amount的最小值
                const max_v = Math.min(...dataInterval.filter((v) => v > ssData[1]));
                //  3.寻找 min_v 所在的下标
                const index = dataInterval.findIndex((v) => v === min_v);
                //  4.计算该amount在y轴上应该展示的位置
                const y_value = ((ssData[1] - min_v) / (max_v - min_v)) * 10 + index * 10;
                ssData[1] = y_value;
                }
            },
            handleData(arr,a2b){
                   //变轴算法。把真实数据按比例投影到对应的区间。
                const newHashArray = []
                for(var i=0;i<arr.length;i++){
                    const obj = arr[i];
                    let bValue = arr[i][1];
                    for(let j=0;j<a2b.length;j++){
                        if(j==a2b.length-1){break;}
                        const A1 = a2b[j][0];
                        const B1 = a2b[j][1];
                        const A2 = a2b[j+1][0];
                        const B2 = a2b[j+1][1];
                        if(bValue>A2 && bValue<= A1){
                            if(bValue<0.1){
                                bValue = B2 + (bValue-A2)/(A1-A2)*(B1-B2);
                                if(bValue>=1700&&bValue<=1800){
                                    bValue+=120;//第一个点
                                }else if(bValue>=1500&&bValue<1600){
                                    bValue+=260;//第一个点
                                }else if(bValue>=1400&&bValue<1500){
                                    bValue+=430;//第三个点
                                }else if(bValue>=1200&&bValue<1300){
                                    bValue+=600;//第四个点
                                }else if(bValue>=1000&&bValue<1100){
                                    bValue+=780;//第五个点
                                }
                            }else{
                                bValue = B2 + (bValue-A2)/(A1-A2)*(B1-B2);
                                if(bValue==2000){
                                    bValue = 1960;
                                }
                                if(bValue>3900&&bValue<4200){
                                    bValue-=300;//正数4个点
                                }
                            }
                            break;
                        }
                    }
                    obj[1] = bValue;
                    newHashArray.push(obj);
                }
                return newHashArray;
