                // const a2bSW = [[10000,12000],[1000,10000],[100,8000],[10,6000],[1,4000],[0.1,2000],[0,0]];
                // this.dataList =  this.handleData(this.dataList,a2bSW);
                // this.init();
                this.init2();
              }
            }
        },
    }
