<template>
    <div class="main-container">
        <PageHeader :B16Fun="B16Fun" :B17Fun="B17Fun" :B33Fun="B33Fun" :B6Fun="B6Fun" :refreshFun="refreshFun"></PageHeader>
        <div class="main-content">
          <!-- <InventoryList :B33="B33" :B6="B6" :isRefresh="isRefresh"></InventoryList> -->
          <InventoryList :isRefresh="isRefresh"></InventoryList>
        </div>
        <!-- <PageFooter :B16="B16" :B17="B17" :B33="B33" :B6="B6" :isRefresh="isRefresh"></PageFooter> -->
        <PageFooter :isRefresh="isRefresh"></PageFooter>
    </div>
</template>
  
  <script>
    import PageHeader from "../components/PageHeader"
    import PageFooter from "../components/PageFooter"
    import InventoryList from "../components/InventoryList"
    export default {
        components: { PageHeader,PageFooter,InventoryList},
        name: 'HelloWorld',
        props: {
            msg: String
        },
        data(){
            return{
                B16:0,
                B17:0,
                B33:-1,
                B6:-1,
                isRefresh:'',
            }
        },
        methods:{
            B16Fun(val){
                this.B16 = parseFloat(val);
            },
            B17Fun(val){
                this.B17 = parseFloat(val);
            },
            B33Fun(val){
                this.B33 = parseFloat(val);
            },
            B6Fun(val){
                this.B6 = parseFloat(val);
            },
            refreshFun(){
                this.isRefresh = this.uuid(16,32);
            },
            uuid:function (len, radix) {
                var chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
                var uuid = [], i;
                radix = radix || chars.length;
                if (len) {
                // Compact form
                for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random()*radix];
                } else {
                // rfc4122, version 4 form
                var r;
                // rfc4122 requires these characters
                uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
                uuid[14] = '4';
                
                // Fill in random data.  At i==19 set the high bits of clock sequence as
                // per rfc4122, sec. 4.1.5
                for (i = 0; i < 36; i++) {
                    if (!uuid[i]) {
                    r = 0 | Math.random()*16;
                    uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
                    }
                }
                }
                return uuid.join('');
            }
        }
    }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style lang="less" scoped>
    .main-container{
        height: 100%;
        display: flex;
        flex-direction: row;
        background-color: #fff;
        .main-content{
                width: 900px;
                flex-grow: 1;
                margin: 12px auto;
                display: flex;
                flex-direction: column;
             .nav {
                width: 100%;
                height: 54px;
                background-color: #fff;
                display: flex;
                flex-direction: row;
                .left{
                    width: 500px;
                    margin-left: 6px;
                }
                .right{
                    flex-grow: 1;
                    margin: auto;
                    text-align: right;
                    margin-right: 20px;
                }
            }
        }
    }

    /deep/ .el-tabs__nav {
        margin: 8px;
    }
    /deep/ .el-tabs__item{
        font-size: 16px;
    }        
  
    /deep/.el-tabs__nav-wrap::after{
        background-color: none;
    }

    /deep/ .el-tabs__header{
        margin: 0 0 0;
    }

    /deep/ .el-tabs__nav-wrap::after{
        height: 0;
    }
  </style>
  